import React from 'react';
import logo from '../../assets/images/logo.svg';
import './App.css';
import ReactDOM from 'react-dom';
//import './index.css';

function countDay() {
  // To set two dates to two variables 
  let startDate = new Date("06/18/2019");
  let todayDate = new Date();
  // To calculate the time difference of two dates 
  let differenceInTime = todayDate.getTime() - startDate.getTime();
  // To calculate the no. of days between two dates 
  let differenceInDay = differenceInTime / (1000 * 3600 * 24);
  return differenceInDay.toFixed(0);
}

class Ivy extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      differenceInDay: 0
    };
  }

  render() {
    const differenceInDay = countDay();
    return (
      <div className="ivy">
        <header className="ivy-header">
          <div className="bg-img"></div>
          {/* <img src={logo} className="ivy-logo" alt="logo" /> */}
          <div className="bg-text">
            <div className="heart heart-main"></div>
            {/* <div className="heart heart-bg"></div> */}
            <div>We have been together in <strong>{differenceInDay}</strong> days</div>
          </div>
          {/* <a
            className="ivy-link"
            href="https://reactjs.org"
            target="_blank"
            rel="noopener noreferrer"
          >
            Learn React
        </a> */}
        </header>
      </div>
    );
  }
}

export default Ivy;
